import { styled } from 'styled-components';

import * as device from '#theme/devices';

/**
 * GridItemLayout is a component that defines the layout of the grid items.
 */

const BaseGridItemLayout = styled.a`
  aspect-ratio: 1;

  @media ${device.maxTablet} {
    aspect-ratio: 3 / 2;

    /* Hide tiles starting from the 4th tile onwards on mobile */
    &:nth-child(n + 4) {
      display: none;
    }
  }
`;

// Grid Layout for 5 tiles
export const GridItemLayout5 = styled(BaseGridItemLayout)`
  @media ${device.tablet} {
    &:nth-child(-n + 2) {
      grid-column-start: span 3;
    }

    &:nth-child(n + 3) {
      grid-column-start: span 2;
    }
  }
`;

// Grid Layout for 4 tiles
export const GridItemLayout4 = styled(BaseGridItemLayout)`
  @media ${device.tablet} {
    &:nth-child(-n + 2) {
      grid-column-start: span 3;
    }

    &:nth-child(n + 2) {
      grid-column-start: span 3;
    }
  }
`;

// Grid Layout for 3 tiles
export const GridItemLayout3 = styled(BaseGridItemLayout)`
  @media ${device.tablet} {
    grid-column-start: span 2;
  }
`;

// Grid Layout for 2 tiles
export const GridItemLayout2 = styled(BaseGridItemLayout)`
  @media ${device.tablet} {
    grid-column-start: span 3;
  }
`;
